	module.exports = {"data":{"postsJson":{"title":"Cierra la Puerta","questionsTitle":"PROFUNDIZANDO MÁS","display":"block","bookName":"2 Juan","ref":"1:1-1:13","html":"<p>   <strong>1</strong><u>1</u>Yo, Juan, el anciano,* les escribo esta carta a la señora elegida y a sus hijos,* a quienes amo en la verdad —y no solo yo sino también todos los que conocen la verdad—, <u>2</u>porque la verdad vive en nosotros y estará con nosotros para siempre.<br />   <u>3</u>La gracia, la misericordia y la paz que provienen de Dios Padre y de Jesucristo —el Hijo del Padre— permanecerán con nosotros, los que vivimos en la verdad y el amor.<br />   <u>4</u>¡Qué contento me puse al encontrarme con algunos de tus hijos y ver que viven de acuerdo con la verdad, tal como el Padre lo ordenó!<br />   <u>5</u>Les escribo para recordarles, queridos amigos,* que nos amemos unos a otros. Este mandamiento no es nuevo, sino que lo hemos tenido desde el principio. <u>6</u>El amor consiste en hacer lo que Dios nos ha ordenado, y él nos ha ordenado que nos amemos unos a otros, tal como ustedes lo oyeron desde el principio.<br />   <u>7</u>Les digo esto, porque muchos engañadores han salido por el mundo. Ellos niegan que Jesucristo vino* en un cuerpo humano. Tales personas son engañadores y anticristos. <u>8</u>Tengan cuidado de no perder lo que hemos* logrado con tanto trabajo. Sean diligentes para que reciban una recompensa completa. <u>9</u>Todo el que se desvía de esta enseñanza no tiene ninguna relación con Dios; pero el que permanece en la enseñanza de Cristo tiene una relación tanto con el Padre como con el Hijo.<br />   <u>10</u>Si a sus reuniones llegara alguien que no enseña la verdad acerca de Cristo, no lo inviten a su casa ni le den ninguna clase de apoyo. <u>11</u>Cualquiera que apoye a ese tipo de gente se hace cómplice de sus malas acciones.<br />   <u>12</u>Tengo mucho más que decirles, pero no quiero hacerlo con papel y tinta. Pues espero visitarlos pronto y hablarles cara a cara. Entonces nuestra alegría será completa.<br />   <u>13</u>Recibe saludos de los hijos de tu hermana,* la elegida por Dios.</p><h5>   1a En griego El anciano.<br />   1b O a la iglesia que Dios ha elegido y a sus miembros.<br />   5 En griego Te ruego, señora.<br />   7 O vendrá.<br />   8 Algunos manuscritos dicen han.<br />   13 O saludos de los miembros de tu iglesia hermana.</h5>","path":"/419","Question1":"¿Acerca de quiénes advierte Juan?","Question2":"¿Según Juan, de qué manera debe cuidarse la iglesia contra el engaño?","Question3":"¿Hay personas a las que no deberías estar escuchando?","Question4":"¿Cómo puedes permanecer en la enseñanza de Cristo?","prevPath":"/418","nextPath":"/420"}},"pathContext":{}}

